*{
	margin: 0px;
	padding: 0px;
    box-sizing: border-box;
    font-family: 'Josefin-Sans', sans-serif;
    font-size: 14px;
}

.current-day{
	background: #000;
	color:#fff;
}
.week-days{
	border: 1px solid;
	text-align: center;
}
td> input{
	width: 50px;
	text-align: center;
	background: transparent;
}
.label-month{
	text-transform: capitalize;
	font-weight: bold;
	text-align: center;
	display: block
}
.label-year{
	text-transform: capitalize;
	font-weight: bold;
	display: block;
	text-align: center;
}
.year-editor{
	width: 100%;
	border: 1px solid #000;
	height: 37px;
	
}
.row{
    margin-bottom: 20px;
}